import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { service } from '../../service/baiviet.service';
import { Injectable } from '@angular/core';
import { Post_TrangLoai } from '../../model/post_trangloai';
import { Breadcrumb} from '../../model/breadcrumb';
import * as $ from 'jquery'; 
@Component({
  selector: 'app-trangloai',
  templateUrl: './trangloai.component.html',
  styleUrls: ['./trangloai.component.css']

})
@Injectable()
export class TrangloaiComponent implements OnInit {

  constructor(private route: ActivatedRoute, private servide: service) { }
  // public idparent: string[];
  Post_TrangLoai: Post_TrangLoai[];
  breadcrumb: Breadcrumb[];
  parent: number;
  idcate: number;
  total: number;
  pageNum: number = 1;
  offset = 20;
  pageNum_chinh: number;
  batdau: number;
  http_api = "http://localhost:8080/game2t_new/api.html?";
  slug_title = "";
  idparent2: number = 2;
  title:string="";
  check_slidebar:number = 0;
  ngOnInit() {
    $('html,body').animate({
      scrollTop: $(".content").offset().top-100
    },100);
    this.gettitle();
    //this.get_parent();

  }
  script() {
    alert("ok");
  }
 
  // async getdata_title2()
  // {
  //   this.idparent2 = await this.gettitle2();
  //   console.log(this.idparent2);
  // } 
  gettitle() {
    this.route.paramMap.subscribe(params => {
      $('html,body').animate({
        scrollTop: $(".content").offset().top-100
      },100);
      this.slug_title = params.get("title");
      this.pageNum = 1;
      this.get_content();
      this.Breadcrumb();
    });
  }
  async Breadcrumb()
  {
    this.parent = await this.servide.get_parent(this.slug_title);   
    if(this.parent!=0){
    this.servide.get_breadcrumb(this.parent)
    .then(resJson => { this.breadcrumb = resJson })
    }
    else{
      this.breadcrumb = [];
    }    
    this.title = await this.servide.get_title(this.slug_title);    
  }
  // console.log(this.pageNum);
  async get_content() {
    //console.log(this.pageNum_chinh+ " - " + this.batdau);
    this.idcate = await this.servide.get_term_id(this.slug_title);
   
    var all_child = await this.servide.get_all_child_con(this.idcate);
    (all_child == '') ? all_child = this.idcate : all_child = all_child + "," + this.idcate;
   // console.log(all_child);
    this.pageNum_chinh = this.pageNum - 1;
    this.batdau = this.pageNum_chinh * this.offset;
    //console.log(this.all_child.split(','));
    //console.log("&id_category=-1&batdau="+this.batdau+"&ketthuc="+this.offset+"&array_id=array()&danhmuc=category&array_category="+this.all_child);
    this.servide.getbaiviet_trangloai("&id_category=-1&batdau=" + this.batdau + "&ketthuc=" + this.offset + "&array_id=&danhmuc=category&array_category=" + all_child)
      .then(resJson => { this.Post_TrangLoai = resJson })
    this.total = await this.servide.get_post_count(all_child);   
    this.check_slidebar = this.total;
  }
  pageChanged(event) {
    $('html,body').animate({
      scrollTop: $(".content").offset().top-100
    },100);
    this.pageNum = event;
    this.get_content();
   
  }
}
