import { Component, OnInit, Input, OnChanges, SimpleChange, SimpleChanges } from '@angular/core';
import { service } from '../../service/baiviet.service';
import * as $ from 'jquery';
import 'sticky-sidebar';

import { TopPost } from '../../model/toppost';
import { TopPost_Video } from '../../model/toppost_video';
declare var StickySidebar: any;
declare var window: any;
@Component({
  selector: 'app-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.css']
})

export class SidebarComponent implements OnChanges, OnInit {

  @Input('thongtin') thongtin: number;
  giatrisau: number;
  giatridau: number;
  toppost: TopPost[];
  
  toppost_video: TopPost_Video[];
  constructor(private service: service) { }
  ngOnInit() {
    this.gettoppost();
    this.gettoppost_video();
    window.FB.XFBML.parse();
  }
  ngOnChanges(changes: SimpleChanges): void {
    let name = changes.thongtin;
    this.giatrisau = name.currentValue;    
    if (this.giatrisau > 0) {      
      this.runjs();
    }

  }
  gettoppost() {
    this.service.gettop_post("").then(resJson => { this.toppost = resJson });
  }
  gettoppost_video() {
    this.service.gettop_post(6).then(resJson => { this.toppost_video = resJson });
  }
  runjs() {   
    setTimeout(function() {  
        var sidebar = new StickySidebar('.content > .w1170 > .col-right', {
          containerSelector: '.content',
          innerWrapperSelector: '.khung-sidebar',
          topSpacing: 50,
          bottomSpacing: 50
        }); 
      },3000);    
  }

}
