import { Component, OnInit, ViewEncapsulation, enableProdMode } from '@angular/core';
enableProdMode();
import { ActivatedRoute } from '@angular/router';
import { Breadcrumb } from '../../model/breadcrumb';
import { service } from '../../service/baiviet.service';
@Component({
  selector: 'app-trangchitiet',
  templateUrl: './trangchitiet.component.html',
  styleUrls: ['./trangchitiet.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class TrangchitietComponent implements OnInit {
  slug: string;
  pageNum: number = 1;
  parent: number;
  breadcrumb: Breadcrumb[];
  title: string = "";
  idcatepost: number = 0;
  display_name: string;
  post_date: string;
  post_content: string;
  post_title: string;
  count_view: number;
  check_slidebar: number = 1;
  constructor(private activatedroute: ActivatedRoute, private service: service) { }
  ngOnInit() {
    this.getslug();    
  }
  getslug() {
    this.activatedroute.paramMap.subscribe(params => {
      $('html,body').animate({
        scrollTop: $(".content").offset().top - 100
      }, 100);
      this.slug = params.get("post");
      this.pageNum = 1;
      this.Breadcrumb();
      this.get_content();
    });
  }
  async Breadcrumb() {
    this.idcatepost = await this.service.get_idcate_ofpost(this.slug);    
    this.parent = await this.service.get_parent_ofid(this.idcatepost);
    this.check_slidebar = this.parent;
    this.service.get_breadcrumb(this.parent)
      .then(resJson => { this.breadcrumb = resJson })
    this.title = await this.service.get_title_post(this.slug);
  }
  async get_content() {
    await this.service.get_PostDetails(this.slug)
      .then(resJson => {
        this.display_name = resJson.display_name;
        this.post_date = resJson.post_date;
        this.post_content = resJson.post_content;
        this.post_title = resJson.post_title;
        this.count_view = resJson.count_view;
      })
    var abc = this.post_content.match(/^(https?\:\/\/)?(www\.youtube\.com|youtu\.?be)\/.+$/gim);
    if (abc.length > 0) {
      abc.forEach((keys: any) => {
        var chuoiyoutube = keys.replace("https://www.youtube.com/watch?v=", "");
        this.post_content = this.post_content.replace(keys, '<p><iframe style="width:100%" height="350" src="https://www.youtube.com/embed/' + chuoiyoutube + '" frameborder="0" allowfullscreen></iframe><p>');
      });
    }

  }


}
