import { Component, OnInit } from '@angular/core';
import { service } from '../../service/baiviet.service';
import * as $ from 'jquery'; 

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.css']
})
export class MenuComponent implements OnInit {

  constructor(private baiviet_service : service) { }

  ngOnInit() {
    this.baiviet_service.getallmenu();    
    this.script();
  }
  hover_menu(id)
  {
    var atr_id = $("#menu"+id).find(".active").attr('id');
    this.baiviet_service.getbaiviet("&id_category="+atr_id+"&batdau=0&ketthuc=4");
  }
  hover_menucon(id)
  {
    this.baiviet_service.getbaiviet("&id_category="+id+"&batdau=0&ketthuc=4");
    //console.log(this.baiviet_service.posts);        
    $(".id_"+id).parent().children().removeClass('active');
    $(".id_"+id).addClass("active");  
  }
 
  script()
  {   
    $(window).scroll(function(){		
			var aTop = $('.menu_pc').height();			
			if($(this).scrollTop()>=aTop+100){
				$(".menu_pc").addClass("menutruoc");	
				$(".menu_pc .w1170 .list_ngang .logo-truot").css({"width":"auto","padding-right":"2%"});			
			}
			else if($(this).scrollTop()<aTop+100)
			{
				$(".menu_pc").removeClass("menutruoc");				
				$(".menu_pc .w1170 .list_ngang .logo-truot").css({"width":"0","padding-right":"0"});			
			}		
		});	

  
  }
}
