import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, RequestMethod} from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { Baiviet_HDT } from '../model/baiviet';
import { Menuall_HDT } from '../model/menuall';
import { Menucon_HDT} from '../model/menucon';
import { Post} from '../model/post';
@Injectable()
export class service
{
    constructor(private http : Http){}
    http_api = "http://localhost:8080/game2t_new/api.html?";
    baiviethdt : Baiviet_HDT[];
    menuallhdt : Menuall_HDT[];
    menuconhdt : Menucon_HDT[];
    
    posts : Post[];
    getbaiviet(data){
        return this.http.get(this.http_api+'function=get_post'+data)
        .toPromise()
        .then(res => res.json())
        .then(resJson => {this.posts = resJson})
        .catch(err => console.log(err));
    }
    getbaiviet_trangloai(data){
        return this.http.get(this.http_api+'function=get_post'+data)
        .toPromise()
        .then(res => res.json())
       // .then(resJson => {this.posts = resJson})
        .catch(err => console.log(err));
    }
    getmenu_cha(){
        return this.http.get(this.http_api+'function=getmenu_cha')
        .toPromise()
        .then(res => res.json())
        .then(resJson => {this.baiviethdt = resJson;})
        .catch(err => console.log(err));
    }
    getmenu_con(parent){
        return this.http.get(this.http_api+'function=getmenu_con&parent='+parent)
        .toPromise()
        .then(res => res.json())  
        .then(resJson => {this.menuconhdt = resJson;})      
        .catch(err => console.log(err));
    }
   
    getallmenu(){
        return this.http.get(this.http_api+'function=getallmenu')
        .toPromise()
        .then(res => res.json())
        .then(resJson => {this.menuallhdt = resJson;})
        .catch(err => console.log(err));
        // 
    }
    guimail(data){    
        
        const headers = new Headers({'Content-Type':'application/json'});
        const requestOptions = new RequestOptions({method : RequestMethod.Post,headers : headers});
        return this.http.post(this.http_api+'function=guimail',JSON.stringify(data), requestOptions)
        .toPromise()
        .then(res => res.text())
        .then(resJson => console.log(resJson));
    }
    async get_term_id(TieuDe)
    {
        const i = await this.http.get(this.http_api+'function=get_term_id&slug='+TieuDe).toPromise();
        return i.json().term_id;
        
    }
    async get_all_child_con(idcate)
    {
        const i = await this.http.get(this.http_api+'function=get_all_child_con&id='+idcate).toPromise();
        return i.json().categories;
        
    }
    async get_post_count(all_child)
    {
        const i = await this.http.get(this.http_api+'function=get_post_count&id_category=-1&batdau=-1&ketthuc=-1&array_id=&danhmuc=category&array_category=' + all_child).toPromise();
        return i.json().tong;
        
    }
    async get_parent(slug)
    {
        const i = await this.http.get(this.http_api+'function=get_parent&slug=' + slug).toPromise();
        return i.json().parent;        
    }
    async get_parent_ofid(id)
    {
        const i = await this.http.get(this.http_api+'function=get_parent_ofid&id=' + id).toPromise();
        return i.json().parent;        
    }
    async get_breadcrumb(parent)
    {      
        return this.http.get(this.http_api+'function=get_breadcrumb&parent=' + parent)
        .toPromise()
        .then(res => res.json())
       // .then(resJson => {this.posts = resJson})
        .catch(err => console.log(err));
    }
    async get_title(slug)
    {
        const i = await this.http.get(this.http_api+'function=get_title&slug=' + slug).toPromise();
        return i.json().name;
        
    }
    async get_title_post(slug)
    {
        const i = await this.http.get(this.http_api+'function=get_title_post&slug=' + slug).toPromise();
        return i.json().post_title;
        
    }
    gettop_post(id_cate)
	{
		return this.http.get(this.http_api+'function=gettop_post&id_cate='+id_cate)
        .toPromise()
        .then(res => res.json())
        //.then(resJson => {this.posts = resJson})
        .catch(err => console.log(err));
    }
    async get_idcate_ofpost(slug)
    {
        const i = await this.http.get(this.http_api+'function=get_idcate_ofpost&slug=' + slug).toPromise();
        return i.json().term_taxonomy_id;        
    }
    get_PostDetails(slug) 
    {
        return this.http.get(this.http_api+'function=get_PostDetails&slug='+slug)
        .toPromise()
        .then(res => res.json())
        //.then(resJson => {this.posts = resJson})
        .catch(err => console.log(err));
    }
    
}